<template>
  <div class="consult_resume">
    <body>
      <div class="container-fluid">
        <div class="submenuimage">
          <p class="subtitle"> </p>
        </div>
        <div>
          <v-tabs centered color="grey darken-3">
            <v-tab to="/resume/insertresume"> </v-tab>
            <v-tab to="/resume/resumeList"> </v-tab>
            <v-tab active to="/resume/consultresume"> </v-tab>
            <v-tab to="/resume/consult">  </v-tab>
            <v-tabs-slider color="deep-purple lighten-5"></v-tabs-slider>
          </v-tabs>
        </div>

        <!--    -->
        <div class="container">
          <b-col>
            <b-card class="viewcontainer">
              <b-form>
                <b-row>
                  <b-col class="rboardwriter"
                    > : {{ rboardDetail.rboardWriter }}<br
                  /></b-col>
                  <p class="rboarddate">
                    {{ formatDate(rboardDetail.rboardDate) }} 
                  </p>
                </b-row>

                <b-row>
                  <b-col class="consulttitle"
                    ><b>{{ rboardDetail.rboardTitle }}</b></b-col
                  >
                </b-row>
                <hr />
                <b-row>
                  <b-col class="consultcontent">{{
                    rboardDetail.rboardContent
                  }}</b-col>
                </b-row>
                <hr />
                <br />
                <b-row v-if="rboardAttachment.rboardNo != null">
                  <b-col class="consultphoto"></b-col>
                  <b-col
                    ><b-button
                      class="qnaphotofile"
                      @click="rboardAttachmentDown(rboardAttachment)"
                      >{{ rboardAttachment.originalFilename }}</b-button
                    ></b-col
                  >
                </b-row>
              </b-form>
            </b-card>
          </b-col>

          <b-row>
            <b-col class="btndiv">
              <b-button to="../../resume/consultresume"></b-button>
              <b-button
                v-if="userData.memberSq === rboardDetail.rboardId"
                class="btn_center"
                @click="updateConsult"
                ></b-button
              >
              <b-button
                v-if="
                  userData.memberSq === rboardDetail.rboardId ||
                    userData.memberEmail === 'admin@kh.com'
                "
                class="btn_center"
                @click="deleteConsult"
                ></b-button
              >
            </b-col>
          </b-row>
        </div>

        <!--  -->
        <b-container>
          <b-row v-for="comment in commentlist" :key="comment.id">
            <b-col>
              <b-card class="text-center">
                <b-row
                  ><b-col cols="2"
                    >{{ comment.memberName }} <br />{{
                      comment.rboardCommentDate | moment("YYYY.MM.DD HH:mm:ss")
                    }}
                  </b-col>
                  <!--      -->
                  <b-col v-if="comment.memberSq != userData.memberSq">{{
                    comment.rboardCommentContent
                  }}</b-col>
                  <!--    input   -->
                  <b-form
                    v-if="
                      userData.memberSq != null &&
                        comment.memberSq == userData.memberSq
                    "
                  >
                    <b-col>
                      <b-row>
                        <b-col>
                          <b-form-textarea
                            :disabled="commentcheck"
                            :value="comment.rboardCommentContent"
                            @input="updateInput"
                            id="commentUptxt"
                          />
                        </b-col>
                        <template v-if="comment.memberSq == userData.memberSq">
                          <b-col>
                            <b-button
                              v-if="userData.memberSq === comment.memberSq"
                              @click="upclick($event)"
                              id="update-btn"
                              ></b-button
                            >
                            <b-button
                              v-if="
                                userData.memberSq === comment.memberSq ||
                                  userData.memberEmail === 'admin@kh.com'
                              "
                              @click="declick(comment.rboardCommentno)"
                              id="deltet-btn"
                              ></b-button
                            >
                            <b-button
                              @click="
                                upendclick(comment.rboardCommentno, $event)
                              "
                              id="updateEnd-btn"
                              ></b-button
                            >
                          </b-col>
                        </template>
                      </b-row>
                    </b-col>
                  </b-form>
                </b-row></b-card
              ></b-col
            >
          </b-row>
          <!--  -->
          <b-form v-if="userData.memberSq != null">
            <b-row
              v-if="
                userData.memberEmail === 'admin@kh.com' ||
                  userData.memberSq === rboardDetail.rboardId ||
                  userData.memberLevel == 2
              "
