<template>
  <b-container fluid>
    <div class="row">
      <div class="submenuimage ">
        <p class="subtitle" id="subtitle"></p>
      </div>
      <div class="container">
        <br />

        <b-row id="writecontain" align-h="end">
          <b-button to="/portfolioenroller"></b-button>
        </b-row>

        <div class="overflow-hidden">
          <!--  -->
          <v-card class="tableborder">
            <v-card-title class="search-bar">
              
              <v-spacer></v-spacer>
              <v-spacer></v-spacer>
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <!-- vuetify data table items     -->
            <v-data-table
              class="row-pointer"
              :headers="headers"
              :items="pboard"
              :search="search"
              item-key="name"
              @click:row="handleClick"
            >
            </v-data-table>
          </v-card>
        </div>
      </div>
    </div>
  </b-container>
</template>

<script>
import { mapState } from "vuex";
//   
// import axios from 'axios';

export default {
  data() {
    return {
      search: "",
      headers: [
        {
          text: "",
          align: "start",
          filterable: false,
          value: "pboardNo",
        },
        //  spring  json    row value  

        { text: "", value: "pboardDivision" },
        { text: "", value: "pboardTitle" },
        { text: "", value: "pboardWriter" },
        { text: "", value: "pboardStatus" },
        { text: "", value: "pboardCount" },
      ],
      // spring     
    };
  },
  computed: {
    ...mapState({
      pboard: (state) => state.pboard,
    }),
  },

  methods: {
    handleClick(value) {
      this.$router.replace({
        name: "Portinfo",
        params: { id: value.pboardNo, number: value.pboardId },
      });
    },
  },
  created() {
    this.$store.dispatch("FETCH_PBOARD");
  },
};
</script>

<style scoped>
* {
  font-family: "Noto Sans KR", sans-serif;
}

.submenuimage {
  background-image: url("../../assets/images/code-1839406_1920.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  opacity: 0.7;
  height: 180px;
  background-color: #f4eeff;
  text-align: center;
  line-height: 180px;
}
.subtitle {
  font-weight: 700;
  color: #fff;
  text-shadow: 2px 2px #4e515763;
  font-size: 50px;
}
#writecontain {
  margin-bottom: 5%;
}
#writecontain > .btn {
  background-color: #424874;
}
.example::-webkit-scrollbar {
  display: none;
}

.row-pointer >>> tbody tr :hover {
  cursor: pointer;
}
.row-pointer >>> thead tr {
  background-color: #ededed;
  border-top: 2px solid #d5d5d5;
}
.row-pointer >>> thead tr th span {
  font-size: 15px;
  font-weight: bold;
  color: #4e5157;
}
</style>
